.wrapper-black {
    background: url('../img/bg.jpg');

    .logo-letter {
        color: #adc41c;
    }

    .checkbox-tools:checked + label {
        box-shadow: 0px 0px 15px 4px #BCCC27;

        .label-title {
            background: #FFFDC9;
        }

        .label-summ {
            color: #88B003;
        }
    }
}


.btn-green {
    background: linear-gradient(283.7deg, #88B003 -19.51%, #BCCC27 107.52%);
    transition: all .3s;

    &:hover {
        background: linear-gradient(140.7deg, #88B003 -19.51%, #BCCC27 107.52%);
    }
}
